.resume-container {
  overflow: scroll;
}

.pdf {
  padding: 5px;
}

#download-btn {
  bottom: 0;
  right: 0;
  position: absolute;
  background: unset;
  border: unset;
}

.resume-container a {
  padding: 0;
}

/* Hide scrollbar for Chrome, Safari and Opera */
.resume-container::-webkit-scrollbar {
  display: none;
}

/* Hide scrollbar for IE and Edge */
.resume-container {
  -ms-overflow-style: none;
}

